header {
  display: -webkit-flex !important;
  display: flex !important;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  height: 70px;
  padding: 20px;
  background-color: #000;
  color: #fff
}

.main {
  margin: 50px 75px;
  text-align: left
}

.center {
  width: 450px;
  margin: 50px auto;
  text-align: center
}

/* Navigation */

.navigation {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end
}

.navigation a {
  margin: 30px;
  color: #fff
}

form {
  /* margin: 0 auto; */
  padding: 75px;
}

form.login {
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#f2f2f2+0,e5e5e5+100 */
  background: #f2f2f2;
  /* Old browsers */
  /* FF3.6-15 */
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #f2f2f2 0%, #e5e5e5 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f2f2f2', endColorstr='#e5e5e5', GradientType=0);
  /* IE6-9 */
  border-radius: 5px;
  box-shadow: 0px 0px 10px #f1f1f1;
}

#background, .editProfile {
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#eef0f4+0,ffffff+100 */
  background: #eef0f4;
  /* Old browsers */
  /* FF3.6-15 */
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #eef0f4 0%, #ffffff 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#eef0f4', endColorstr='#ffffff', GradientType=0);
  /* IE6-9 */
}

/* Images */

.imageBanner {
  position: relative;
  height: 442px;
  background-image: linear-gradient(to left top, #919191, #ababab, #c7c7c7, #e2e2e2, #ffffff);
  overflow: hidden
}

.imageBanner.set {
  background-image: none
}

.textOverlay {
  position: absolute;
  top: 60%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
}

/* FontAwesome */

.svg-inline--fa {
  margin: 0 5px;
  cursor: pointer
}


/* Buttons */

button {
  width: 300px;
  margin: 25px auto !important;
}

button.circle {
  width: auto;
  width: initial;
  border-radius: 50%;
  padding: 2px;
  font-size: 1em;
  margin: 0 5px !important
}

.btn.btn-link {
  border-bottom: 1px solid #dfe0e6;
  color: #000;
  text-decoration: none;
  font-size: 14px !important;
}

.btn.active.btn-link {
  border-bottom: 3px solid #06b367;
  text-decoration: none
}

.btn.btn-link:hover {
  color: #000;
  color: initial;
  text-decoration: none
}


/* Links */

.link {
  cursor: pointer
}

/* Dashboard */

.statistics {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  margin: 50px auto;
}

.statistics > div {
  width: 290px;
  height: 190px;
  padding: 30px;
  text-align: center;
  border-radius: 10px;
  color: #fff
}

.statistics .svg-inline--fa {
  font-size: 2em
}

.statistics>div div:nth-child(2) {
  font-size: 3em
}

.page {
  -webkit-flex: 0 0 30%;
          flex: 0 0 30%;
  width: 395px;
  height: 105px;
  border-radius: 7px;
  box-shadow: 0 5px 15px #d2d2d2;
  margin: 1em;
  padding: 1em
}

.page p {
  margin: 0
}

/* Images */

.profilePic {
  background-color: #06b367;
  width: 75px;
  height: 75px;
  border-radius: 50%;
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center
}


/* Preview Text */

.previewText {
  /* autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  display: -webkit-box;
  -webkit-line-clamp: 2;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 12px;
  color: #333;
  font-weight: lighter
}



/* Custom Input */

.verificationInput {
  text-align: center;
  border: none !important;
  font-size: 30px !important;
}

.smInput {
  height: 55px !important;
}

.smLabel {
  font-size: 18px
}

.smSettings .input-group-text {
  background-color: transparent !important
}

.form-control::-webkit-input-placeholder {
  color: #a5a5a8 !important
}

.form-control::-ms-input-placeholder {
  color: #a5a5a8 !important
}

.form-control::placeholder {
  color: #a5a5a8 !important
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white
}

.App-link {
  color: #61dafb
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* Transitions */

.fade-appear {
  opacity: 0.01;
}

.fade-appear.fade-appear-active {
  opacity: 1;
  transition: opacity .5s ease-in;
}

/* Phone Graphic View */

/* The device with borders */

.smartphone {
  position: relative;
  width: 410px;
  height: 900px;
  margin: auto;
  border: 16px black solid;
  border-top-width: 60px;
  border-bottom-width: 60px;
  border-radius: 36px;
}

/* The horizontal line on the top of the device */

.smartphone:before {
  content: '';
  display: block;
  width: 60px;
  height: 5px;
  position: absolute;
  top: -30px;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: #333;
  border-radius: 10px;
}

/* The circle on the bottom of the device */

.smartphone:after {
  content: '';
  display: block;
  width: 35px;
  height: 35px;
  position: fixed;
  left: 50%;
  bottom: -65px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: #333;
  border-radius: 50%;
}

/* The screen (or content) of the device */

.smartphone .content {
  width: 100%;
  height: 640px;
  /* background: white; */
}

/* Social Media */

.social a {
  margin: 0 5px
}

/* Position and sizing of burger button */

.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  right: 20px;
  top: 20px;
}

/* Color/shape of burger icon bars */

.bm-burger-bars {
  background: #fff;
}

/* Color/shape of burger icon bars on hover*/

.bm-burger-bars-hover {
  background: #a90000;
}

/* Position and sizing of clickable cross button */

.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */

.bm-cross {
  background: #bdc3c7;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/

/* .bm-menu-wrap {
  position: fixed;
  height: 100%;
} */

/* General sidebar styles */

.bm-menu {
  background: #000;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */

.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */

.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
}

/* Individual item */

.bm-item {
  display: inline-block;
  color: #fff;
}

.bm-item a {
  color: #fff;
}

/* Styling of overlay */

.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

.bm-burger-button {
  display: none;
}

/* Responseiveness */

@media (max-width: 767px) {
  .navigation {
    display: none;
  }
  .bm-burger-button {
    display: inline;
    display: initial;
  }
  /* Dashboard */
  .statistics {
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }

  .statistics > div {
    margin: 15px;
    width: 40%;
  }
}
